<template>
  <div>
    <el-card>
      <el-row v-for="index in info">
        <el-col :span='4'>{{index.key}}</el-col>
        <el-col :span='10'>{{index.value}}</el-col>
      </el-row>
    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    return {
      info: [
        { key: '项目名称', value: '电商后台管理系统' },
        { key: '开发人员', value: 'Albert' },
        { key: '运行环境', value: 'Node' },
        { key: '前端框架', value: 'Vue.js' },
        { key: '版本号', value: '2.0' },
        { key: '开始日期', value: '2020.6.5' },
        { key: '结束日期', value: '2020.7.7' },
        { key: 'QQ', value: '1030658764' },
      ]
    }
  }
}
</script>
<style lang="less" scoped>
.el-row{border: 1px solid #ddd;padding: 10px 2px;text-indent: 2em;}
.el-row .el-col:first-child{border-right: solid 1px #ddd;}
</style>


